import React from "react";
import "./App.css";
// msg class
const Msg = (props) => {
  let msg = props.msg;
  let className = props.class;
  return (
    <div
      className={className}
      style={{ backgroundColor: msg.colour, padding: 10, marginTop: 5 }}
    >
      Time:{msg.time} - Room:{msg.room}
      <br />
      From:{msg.from} - {msg.text}
    </div>
  );
};
export default Msg;
