import React, { useReducer, useEffect, Fragment } from "react";
import io from "socket.io-client";
import { MuiThemeProvider } from "@material-ui/core/styles";
import Chat from "./chat.png";
import "./App.css";
import Person from "./person.png";
import {
  Toolbar,
  AppBar,
  MenuItem,
  Typography,
  TextField,
  Button,
  Select,
} from "@material-ui/core";
import Modal from "react-modal";
import theme from "./theme";
import Msg from "./msg";
const Main = () => {
  const initialState = {
    mssgs: [],
    status: "",
    showJoinFields: true,
    alreadyExists: false,
    chatName: "",
    roomName: "",
    typingMsg: "",
    isType: false,
    message: "",
    roomsName: [],
    usersinRoom: [],
    show: false,
  };
  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);
  const handleClose = () =>
    setState({
      show: false,
    });
  const handleShow = () =>
    setState({
      show: true,
    });
  const onWelcome = (dataFromServer) => {
    addMessage(dataFromServer);
    setState({
      showJoinFields: false,
      alreadyExists: false,
    });
  };
  const onExists = (dataFromServer) => {
    setState({
      status: dataFromServer.text,
    });
  };
  const getAllrooms = (rooms) => {
    setState({
      roomsName: rooms.allrooms,
    });
  };
  const getAllusers = (users) => {
    let usersin = [];
    for (let i = 0; i < users.allusers.length; i++) {
      usersin.push(users.allusers[i].name);
    }
    setState({
      usersinRoom: usersin,
    });
  };

  const onTyping = (dataFromServer) => {
    if (dataFromServer.from !== state.chatName) {
      setState({
        typingMsg: dataFromServer.text,
      });
    }
  };
  const onNewMessage = (dataFromServer) => {
    addMessage(dataFromServer);
    setState({
      typingMsg: "",
      message: "",
    });
  };
  useEffect(() => {
    serverConnect();
  }, []);
  const serverConnect = () => {
    //const socket = io.connect("localhost:5000", { forceNew: true });
    const socket = io();
    socket.on("allRooms", getAllrooms);
    socket.on("allusers", getAllusers);
    socket.on("nameexists", onExists);
    socket.on("welcome", onWelcome);
    socket.on("someonejoined", addMessage);
    socket.on("someoneleft", addMessage);
    socket.on("someoneistyping", onTyping);
    socket.on("newmessage", onNewMessage);
    setState({
      socket: socket,
    });
  };
  const addMessage = (dataFromServer) => {
    let mssgs = state.mssgs;
    mssgs.push(dataFromServer);
    setState({
      mssgs: mssgs,
    });
  };
  const onMessageChange = (e) => {
    setState({
      message: e.target.value,
    });
    if (state.isType === false) {
      state.socket.emit(
        "typing",
        {
          from: state.chatName,
        },
        (err) => {}
      );
      setState({
        isType: true,
      });
    }
  };
  const sendMsg = (e) => {
    if (state.message !== "") {
      state.socket.emit(
        "message",
        {
          from: state.chatName,
          text: state.message,
        },
        (err) => {}
      );
      setState({
        isType: false,
      });
    }
  };
  const handleJoin = () => {
    state.socket.emit("join", {
      chatName: state.chatName,
      roomName: state.roomName,
    });
  };
  const onNameChange = (e) => {
    setState({
      chatName: e.target.value,
    });
  };

  const afterOpenModal = () => {
    console.log(state.usersinRoom);
    console.log(state.usersinRoom.length);
  };

  const onRoomChange = (e) => {
    console.log(e.target.value);
    console.log(state.roomsName);

    setState({
      roomName: e.target.value,
    });
  };

  return (
    <MuiThemeProvider theme={theme} color="secondary">
      <AppBar position="static" color="green">
        <Toolbar>
          <Typography variant="h6">My chat - INFO 3139</Typography>
          {!state.showJoinFields && (
            <img
              src={Person}
              alt="planet"
              onClick={() => handleShow()}
              style={{
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                width: "30px",
                height: "30px",
              }}
            />
          )}
        </Toolbar>
      </AppBar>
      {state.showJoinFields && (
        <img
          src={Chat}
          alt="planet"
          style={{
            display: "block",
            marginLeft: "auto",
            marginRight: "auto",
            width: "40%",
          }}
        />
      )}
      {state.showJoinFields && (
        <h3
          style={{
            textAlign: "center",
            color: "red",
          }}
        >
          Sign In
        </h3>
      )}
      {state.showJoinFields && (
        <Fragment>
          <div className="chatNameBlock">
            <TextField
              onChange={onNameChange}
              placeholder="your name"
              autoFocus={true}
              required
              value={state.chatName}
              error={state.status !== ""}
              helperText={state.status}
            />
            <p> </p>
          </div>
          <br />
          <br />
          <div className="chatNameBlock" id="secBlock">
            <p> Join exiting or Enter new room Name </p>
            <Select onChange={onRoomChange}>
              {state.roomsName.map((room) => (
                <MenuItem value={room}> {room} </MenuItem>
              ))}
            </Select>
            <br />
            <br />
            <TextField
              onChange={onRoomChange}
              placeholder="room name"
              autoFocus={true}
              required
              value={state.roomName}
              error={state.status !== ""}
              helperText={state.status}
            />
            <p> </p>
          </div>
          <Button
            variant="contained"
            color="secondary"
            style={{
              marginLeft: "42%",
              marginTop: "15px",
            }}
            onClick={() => handleJoin()}
            disabled={state.chatName === ""}
          >
            Join
          </Button>
        </Fragment>
      )}
      <div className="scenario-container">
        {state.mssgs.map((message, index) => (
          <Msg
            msg={message}
            class={message.from === state.chatName ? "me" : "others"}
            key={index}
          />
        ))}
      </div>
      {!state.showJoinFields && (
        <TextField
          onChange={onMessageChange}
          placeholder="hi..."
          style={{
            marginLeft: "12%",
            marginTop: "15px",
          }}
          autoFocus={true}
          value={state.message}
          onKeyPress={(e) => (e.key === "Enter" ? sendMsg() : null)}
        />
      )}
      <div>
        <Typography color="primary"> {state.typingMsg} </Typography>
      </div>
      <Modal
        isOpen={state.show}
        onAfterOpen={afterOpenModal}
        onRequestClose={handleClose}
      >
        <h1> Online</h1>
        <div className="allusers">
          {state.usersinRoom.map((user) => (
            <div className="onlineperson"> {user} </div>
          ))}
        </div>
      </Modal>
    </MuiThemeProvider>
  );
};
export default Main;
